"use client";

import { useMemo, useState, useEffect, useCallback } from "react";
import { useRouter, useSearchParams, usePathname } from "next/navigation";
import type {
  ColumnFiltersState,
  SortingState,
  PaginationState,
  Table as TableType,
} from "@tanstack/react-table";
import { DataTable } from "@/app/components/data-table";
import { productColumns } from "./columns";
import { ProductsTableToolbar } from "./products-table-toolbar";
import type { ProductWithCategory } from "@/types/product";
import { Button } from "@/app/components/ui/button";
import Link from "next/link";
import { ROUTES } from "@/app/routes";
import type { Category } from "@/supabase/types";

interface ProductsDataTableProps {
  products: ProductWithCategory[];
  total: number;
  categories: Category[];
}

export function ProductsDataTable({
  products,
  total,
  categories,
}: ProductsDataTableProps) {
  const router = useRouter();
  const searchParams = useSearchParams();
  const pathname = usePathname();

  // Initialize state from URL
  const [sorting, setSorting] = useState<SortingState>([]);
  // columnVisibility is client-side only usually, but we can keep it here.
  // Actually the original code didn't sync visibility to URL, just local state.
  // DataTable handles visibility internally if not passed.
  // checking original code:
  // const [columnVisibility, setColumnVisibility] = useState<VisibilityState>({});
  // passed to useReactTable.
  // So we don't need to pass it if we don't want to control it.
  // But let's keep it simple. If we don't pass it, DataTable will use its internal state.
  // It's better to let DataTable handle it unless we need to persist it.
  // Original code didn't persist it. So I will omit it and let DataTable handle it.

  const [pagination, setPagination] = useState<PaginationState>({
    pageIndex: searchParams.get("page")
      ? Number(searchParams.get("page")) - 1
      : 0,
    pageSize: 20, // Default limit
  });

  const [columnFilters, setColumnFilters] = useState<ColumnFiltersState>([]);

  // Sync initial filters from URL on mount
  useEffect(() => {
    const filters: ColumnFiltersState = [];
    if (searchParams.has("search")) {
      filters.push({ id: "name", value: searchParams.get("search") });
    }
    const categoryParams = searchParams.getAll("category");
    if (categoryParams.length > 0) {
      filters.push({ id: "category_id", value: categoryParams });
    }
    const statusParams = searchParams.getAll("status");
    if (statusParams.length > 0) {
      filters.push({ id: "status", value: statusParams });
    }
    setColumnFilters(filters);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []); // Run once on mount

  const data = useMemo(() => products, [products]);

  // Sync state changes to URL
  const createQueryString = useCallback(
    (params: Record<string, string | number | null | undefined | string[]>) => {
      const newSearchParams = new URLSearchParams(searchParams.toString());

      for (const [key, value] of Object.entries(params)) {
        if (value === null || value === undefined || value === "") {
          newSearchParams.delete(key);
        } else if (Array.isArray(value)) {
          newSearchParams.delete(key);
          value.forEach((v) => newSearchParams.append(key, v));
        } else {
          newSearchParams.set(key, String(value));
        }
      }

      return newSearchParams.toString();
    },
    [searchParams],
  );

  // Debounced URL update effect
  useEffect(() => {
    const timeoutId = setTimeout(() => {
      const newFilters = columnFilters.reduce(
        (acc, filter) => {
          if (filter.id === "name") {
            return { ...acc, search: filter.value as string };
          }
          if (filter.id === "category_id") {
            return { ...acc, category: filter.value as string[] };
          }
          if (filter.id === "status") {
            return { ...acc, status: filter.value as string[] };
          }
          return acc;
        },
        {} as Record<string, unknown>,
      );

      // Add pagination
      const page = pagination.pageIndex + 1;

      // Check if we need to remove keys not present in newFilters
      const currentKeys = ["search", "category", "status"];
      const keysToRemove = currentKeys.filter((k) => {
        if (k === "search" && !columnFilters.find((f) => f.id === "name"))
          return true;
        if (
          k === "category" &&
          !columnFilters.find((f) => f.id === "category_id")
        )
          return true;
        if (k === "status" && !columnFilters.find((f) => f.id === "status"))
          return true;
        return false;
      });

      const removalParams = keysToRemove.reduce(
        (acc, k) => ({ ...acc, [k]: null }),
        {},
      );

      const queryString = createQueryString({
        ...newFilters,
        ...removalParams,
        page: page > 1 ? page : null,
      });

      router.push(`${pathname}?${queryString}`, { scroll: false });
    }, 300);

    return () => clearTimeout(timeoutId);
  }, [
    columnFilters,
    pagination.pageIndex,
    createQueryString,
    pathname,
    router,
  ]);

  if (products.length === 0 && total === 0 && columnFilters.length === 0) {
    return (
      <div className="rounded-lg border py-12 text-center">
        <p className="text-muted-foreground mb-4">Henüz ürün eklenmemiş.</p>
        <Button asChild>
          <Link href={ROUTES.PRODUCTS.NEW}>İlk Ürünü Ekle</Link>
        </Button>
      </div>
    );
  }

  return (
    <div className="flex flex-col gap-4">
      <DataTable
        columns={productColumns}
        data={data}
        pageCount={Math.ceil(total / pagination.pageSize)}
        pagination={pagination}
        onPaginationChange={setPagination}
        sorting={sorting}
        onSortingChange={setSorting}
        columnFilters={columnFilters}
        onColumnFiltersChange={setColumnFilters}
        toolbar={(table: TableType<ProductWithCategory>) => (
          <ProductsTableToolbar table={table} categories={categories} />
        )}
      />
      <div className="text-muted-foreground text-sm">Toplam {total} ürün</div>
    </div>
  );
}
